import Vue from 'vue';
import Router from 'vue-router';

import HomePage from '../home/HomePage.vue'
import RobotBuilder from '../build/RobotBuilder.vue';
import PartInfo from '../parts/PartInfo.vue';
import BrowseParts from '../parts/BrowseParts.vue';
import RobotHeads from '../parts/RobotHeads.vue';
import RobotArms from "../parts/RobotArms.vue";
import RobotTorsos from "@/parts/RobotTorsos.vue";
import RobotBases from "@/parts/RobotBases.vue";
import SidebarStandard from "@/sidebars/SidebarStandard";
import SidebarBuild from "@/sidebars/SidebarBuild";
import ShoppingCart from "@/cart/ShoppingCart";

Vue.use(Router);

export default new Router({
    mode:'history',
    routes: [{
        path: '/',
        name: 'Home',
        // component: HomePage, from simple property component object to keyed object
        components:{
            default: HomePage,
            sidebar: SidebarStandard
        },
    }, {
        path: '/build',
        name: 'Build',
        // component: RobotBuilder,
        components:{
            default: RobotBuilder,
            sidebar: SidebarBuild,
        },
    },{
        path:'/parts/browse',
        name:'BrowseParts',
        component:BrowseParts,
        children:[
            {
                name:'BrowseHeads',
                path:'heads',
                component: RobotHeads,
            },{
                name:'BrowseArms',
                path:'arms',
                component: RobotArms,
            },{
                name:'BrowseTorsos',
                path:'torsos',
                component: RobotTorsos,
            },{
                name:'BrowseBases',
                path:'bases',
                component: RobotBases,
            },

        ]
    },{
        path:'/parts/:partType/:id',
        name:'Parts',
        component:PartInfo,
        props:true,
            /*why does this work like this*/
            /*passes the params that it
            identifies as rout as
            props instead of route params */
         beforeEnter(to, from,next){
            const isValidId =Number.isInteger(Number(to.params.id) );
            next(isValidId);
            },
    },{
        path:'/cart',
        //typo here  without the / == loads of debug
        name:'Cart',
        component:ShoppingCart,
        }],
});